
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.photo-upload {
  height: auto;
  margin: 20px 0;
  position: relative;
  text-align: center;
  width: 100%;
}
.input-file {
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  width: 0.1px;
  z-index: -1;
}
.input-file:focus + label {
  border-color: #0070d3;
}
.input-file + label {
  background: transparent;
  border: 2px solid #bc0087;
  border-radius: 50%;
  color: #5b5b5b;
  display: inline-block;
  font-size: 12pt;
  font-weight: 500;
  height: 150px;
  line-height: 150px;
  text-align: center;
  width: 150px;
}
.input-file + label:hover,
.input-file + label:focus {
  border-color: #0070d3;
  cursor: pointer;
}
.image-preview {
  border-radius: 50%;
  height: 150px;
  left: 0;
  margin: 0 auto;
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 0;
  width: 150px;
}
.image-preview__img {
  border: 2px solid #bc0087;
  border-radius: 50%;
  height: 150px;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: center;
     object-position: center;
  width: 150px;
}


